.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.new-line {
  white-space: pre-line;
}

div.optionButtons {
  float:left;
}
div.container {
padding: 10px 50px 10px 50px;
}

button.optionButtons {
  width: 50px;
  height: 50px;
  margin-left: 30px;
}
.labelResponse {
  padding: 10px;
}
.text-right {
  text-align: right;
}
.pointer {
  cursor: pointer;
}
ul li.sameline {
  display:flex;
  list-style:none;
}
img.questionImage {
  width: 400px;
}
.rect {
  fill:rgb(0,0,255);stroke-width:3;stroke:rgb(0,0,0)
}
div.wrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
  grid-auto-rows: minmax(100px, auto);
}
.grid-image {
  grid-column: 1;
  grid-row:  1;
}
.grid-question {
  grid-column: 1;
  grid-row: 3;
}
.grid-responses {
  grid-column: 1;
  grid-row: 2;
}
.grid-score {
  grid-column: 1;
  grid-row: 2;
  height: fit-content;
}
.border-blue {
  border: 5px solid green;
  margin-left: 2px;
  margin-right: 2px;
  cursor: pointer;
}
.border-red {
  border: 5px solid red;
  margin-left: 2px;
  margin-right: 2px;
  cursor: pointer;
}
